<template>
  <div class="banner-warp">
    <swiper :options="swiperOption" v-if="banners.length">
      <swiper-slide v-for="item of banners" :key="item.goods">
        <router-link :to="'/app/home/productDetail/'+item.goods" target = _blank> <img :src="item.image" alt="" /></router-link>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>
<style>
  .banner-warp{
    height:300px;
  }
</style>



<script>
  import 'swiper/dist/css/swiper.css'
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  import {bannerGoods} from '../../api/api'

  export default {
    components: {
      swiper,
      swiperSlide,
    },
    data() {

      return {
        swiperOption: {
          pagination: {el:'.swiper-pagination'},
          slidesPerView:1,
          paginationClickable: true,
          autoplay: {delay:2000, disableOnInteraction:false},
          spaceBetween:30,
          loop:true
        },
        banners:[]

      }

    },
    methods:{
      getBanner(){
        bannerGoods()
          .then((response)=> {
            console.log(response)
            //跳转到首页页response.body面
            this.banners = response.data
            console.dir(this.banners)
          })
          .catch(function (error) {
            console.log(error);
          });
      }
    },
    created(){
      this.getBanner();
    }

  }

</script>



